export default {
	props: {
		items:{
			type:Array
		},
		index:{
			type: Number,
			default: -1
		}
		
	},
	data() {
		return {
			activeIndex: this.index
		};
	},
	methods: {
		tapTab(index){
			this.activeIndex = index
			this.$emit("change",index)
		}
	}
}
